import React, {  useRef, useEffect , useState } from "react";
import styled from "styled-components";
import { motion } from "framer-motion";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { useLocomotiveScroll } from "react-locomotive-scroll";
import { faVolumeOff } from "@fortawesome/free-solid-svg-icons";
import { faVolumeHigh } from "@fortawesome/free-solid-svg-icons";
import music from "../assets/music.mp3"
import { Link } from "react-router-dom";



const NavContainer = styled(motion.div)`
  width: 100vw;
  z-index: 6;
  position: absolute;
  top: ${(props) => (props.click ? "0" : `-${props.theme.navHeight}`)};

  display: flex;
  justify-content: center;
  align-items: center;

  transition: all 0.3s ease;

  @media (max-width: 40em) {
    top: ${(props) => (props.click ? "0" : `calc(-50vh - 4rem)`)};
  }
`;
const MenuItemss = styled(motion.ul)`
  position: relative;
  height: ${(props) => props.theme.navHeight};
  background-color: ${(props) => props.theme.body};
  color: ${(props) => props.theme.text};
  list-style: none;

  display: flex;
  justify-content: space-around;
  align-items: center;

  width: 100%;
  padding: 0 10rem;

  @media (max-width: 40em) {
    flex-direction: column;
    padding: 2rem 0;
    height: 50vh;
  }
`;

const MenuBtn = styled.li`
  background-color: ${(props) => `rgba(${props.theme.textRgba}, 0.7)`};
  list-style-type: style none;
  color: ${(props) => props.theme.body};
  width: 15rem;
  height: 2.5rem;

  clip-path: polygon(0 0, 100% 0, 80% 100%, 20% 100%);

  position: absolute;
  top: 100%;
  left: 50%;
  transform: translateX(-50%);

  display: flex;
  justify-content: center;
  align-items: center;

  font-size: ${(props) => props.theme.fontmd};
  font-weight: 600;
  text-transform: uppercase;

  cursor: pointer;

  @media (max-width: 40em) {
    width: 10rem;
    height: 2rem;
  }
`;
const MenuItems = styled(motion.li)`
  text-transform: uppercase;
  color: ${(props) => props.theme.text};
  cursor: pointer;
  @media (max-width: 40em) {
    flex-direction: column;
    padding: 0.5rem 0;
  }
`;

const NavBar = () => {
const ref = useRef(null)

  const [click, setClick] = useState(false);
const [ClickMusic , setClickmusic] = useState(false)
const [icon , setIcon]= useState(false)




function clickicon(){
  setIcon(icon => !icon)
}
let toogleicon = icon ? faVolumeHigh : faVolumeOff

const musicon = () => {
  setClickmusic(!ClickMusic)
  if(!ClickMusic ){
    ref.current.play()
    
  }else{
    ref.current.pause()
  }
}

  const { scroll } = useLocomotiveScroll();


console.log(icon);

  return (

     <NavContainer
      click={+click}
      initial={{
        y: "-100%",
      }}
      animate={{
        y: 0,
      }}
      transition={{
        duration: 2,
        delay: 5,
      }}
    >
      <MenuItemss
        drag="y"
        dragConstraints={{
          top: 0,
          bottom: 70,
        }}
        dragElastic={0.05}
        dragSnapToOrigin
      >
        <MenuBtn onClick={() => setClick(!click)}>Menu</MenuBtn>
        <MenuItems  onClick={() => musicon()} >
       
       
        <div  className="btns">
   
     <FontAwesomeIcon  onClick={clickicon} icon={toogleicon} />
       <audio src={music} ref={ref} loop />
        </div>
        </MenuItems>
    <Link to="/">
    <MenuItems
          
          whileHover={{ scale: 1.1, y: -5 }}
          whileTap={{ scale: 0.9, Y: 0 }}
        >
          Uyga
        </MenuItems></Link>
      <Link to="/about">
      <MenuItems

whileHover={{ scale: 1.1, y: -5 }}
whileTap={{ scale: 0.9, Y: 0 }}
>
Men Haqimda
</MenuItems>
      </Link>
      <Link to="/skills">
     <MenuItems
        
        whileHover={{ scale: 1.1, y: -5 }}
        whileTap={{ scale: 0.9, Y: 0 }}
      >
        Tajriba
      </MenuItems>
     </Link>
     <Link to="/project">
     <MenuItems
        
        whileHover={{ scale: 1.1, y: -5 }}
        whileTap={{ scale: 0.9, Y: 0 }}
      >
        Loyixalar
      </MenuItems>
     </Link>
       <Link to="/contact">
       <MenuItems
   
   whileHover={{ scale: 1.1, y: -5 }}
   whileTap={{ scale: 0.9, Y: 0 }}
 >
  Bog'lanish
 </MenuItems>

       </Link>
     
      </MenuItemss>
    </NavContainer>

  );
};

export default NavBar;
