import React from 'react';
import Faqs  from "../components/Faq_Data.json"
import "../components/style.css"
import styled from "styled-components";
import {
    Accordion,
    AccordionItem,
    AccordionButton,
    AccordionPanel,
    AccordionIcon,
    Box
  } from '@chakra-ui/react'
const Faqss = () => {
    return (
        <div>
<Accordion className='faqs' >
{Faqs.map(faq => (
  <AccordionItem className='faq_item' key={faq.id} >
  <h2>
    <AccordionButton className='btn_quetion'>
      <Box flex='1' textAlign='center'>
       {faq.savol}
      </Box>
      <AccordionIcon />
    </AccordionButton>
  </h2>
  <AccordionPanel className='answer' textAlign="center"    pb={4}>
  {faq.javob}
  </AccordionPanel>
</AccordionItem>


)) }
</Accordion>            
        </div>
    );
};


export default Faqss;